.codecontent {
  background-image: url(bg.jpg);
  background-repeat: no-repeat;
  position: absolute;
  background-size: cover;
  height: 100%;
  width: 100%;
}
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  overflow: scroll;
  overflow-x: hidden;
  background: linear-gradient(180deg, #845007 0%, rgba(196, 196, 196, 0) 100%);
}
.codecontent .login-Link {
  display: none;
}
.codecontent h4 {
  color: white;
  font-size: 18px;
  line-height: 40px;
  background-color: #3a4660;
  width: 91%;
  margin: 0 auto;
  position: relative;
  top: 9em;
  border-radius: 49px;
  border: 2px solid #845007;
}

.code-p {
  background-color: white;
  color: #3a4660;
  position: relative;
  top: 10em;
  height: 18%;
  width: 90%;
  margin: 0 auto;
  font-weight: 800;
  font-size: 17px;
  line-height: 40px;
  text-shadow: none;
  border-radius: 30px;
  padding-top: 1em;
  padding-left: 1em;
  border: 2px solid #845007;
  margin-top: 1em;
}
