.logo {
  width: 104px;
  height: 99px;
  position: absolute;
  left: 1em;
  top: 1em;
  background: linear-gradient(180deg, #3a4660 0%, rgba(58, 70, 96, 0) 100%);
}

@import url("https://fonts.googleapis.com/css2?family=Rouge+Script&display=swap");
.logo p {
  font-family: "Rouge Script", cursive;
  font-style: normal;
  font-weight: normal;
  font-size: 55px;
  position: absolute;
  left: 37px;
  top: -0.7em;
  display: flex;

  color: #ffffff;
}
@import url("https://fonts.googleapis.com/css2?family=Ruthie&display=swap");
h3 {
  font-family: Ruthie;
  font-style: normal;
  font-weight: normal;
  font-size: 60px;
  display: flex;
  position: absolute;
  left: 2.5em;
  top: -0.5em;
  color: #3a4660;
}
.navbar .login-Link {
  font-family: cursive;
  font-style: normal;
  font-weight: 800;
  font-size:40px;
  position: absolute;
  left: 33em;
  top: 0.3em;
  line-height: 56px;
  text-align: center;
  text-decoration: none;
  color: #fefcfc;
}
.navbar .login-Link:hover {
  color: #3a4660;
}
.navbar {
  width: 100%;
}
.navbar .acc {
  background: #3a4660;
  width: 100px;
  height: 90px;
  border-radius: 50%;
  position: absolute;
  left: 88em;
  top: 1em;
  cursor: pointer;
}
.acc p {
  font-family: Rufina;
  font-size: 55px;
  position: absolute;
  left: 0.5em;
  top: -0.8em;
  color: white;
}
