@import url(https://fonts.googleapis.com/css2?family=Rouge+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ruthie&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
@import url(//db.onlinewebfonts.com/c/c373d9005cc5dfa6115be09d6d1a6dc9?family=Rockwell+Nova);
@import url(//db.onlinewebfonts.com/c/c373d9005cc5dfa6115be09d6d1a6dc9?family=Rockwell+Nova);
body,
html {
  height: 100%;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.52);
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #3a4660;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  width: 500px;
  right: 6.2em;
  height: 90px;
  border-radius: 46px;
}
.dropdown-content a {
  color: white;
  margin-left: 1.5em;
  margin-top: 0.8em;
  padding: 1em 2em;
  text-decoration: none;
  display: inline-block;
  border: 3px solid #845007;
  border-radius: 34px;
  font-size: 18px;
}
.acc:hover .dropdown-content {
  display: block;
}
.dropdown-content a:hover {
  background-color: #a08560;
}

.main {
  background-image: url(/sm/static/media/bg.17dd4346.jpg);
  background-repeat: no-repeat;
  position: absolute;
  background-size: cover;
  height: 100%;
  width: 100%;
}
.container {
  height: 100%;
  width: 100%;
  position: absolute;

  background: linear-gradient(180deg, #845007 0%, rgba(196, 196, 196, 0) 100%);
}
.main .navbar .acc {
  display: none;
}

.logo {
  width: 104px;
  height: 99px;
  position: absolute;
  left: 1em;
  top: 1em;
  background: linear-gradient(180deg, #3a4660 0%, rgba(58, 70, 96, 0) 100%);
}
.logo p {
  font-family: "Rouge Script", cursive;
  font-style: normal;
  font-weight: normal;
  font-size: 55px;
  position: absolute;
  left: 37px;
  top: -0.7em;
  display: flex;

  color: #ffffff;
}
h3 {
  font-family: Ruthie;
  font-style: normal;
  font-weight: normal;
  font-size: 60px;
  display: flex;
  position: absolute;
  left: 2.5em;
  top: -0.5em;
  color: #3a4660;
}
.navbar .login-Link {
  font-family: cursive;
  font-style: normal;
  font-weight: 800;
  font-size:40px;
  position: absolute;
  left: 33em;
  top: 0.3em;
  line-height: 56px;
  text-align: center;
  text-decoration: none;
  color: #fefcfc;
}
.navbar .login-Link:hover {
  color: #3a4660;
}
.navbar {
  width: 100%;
}
.navbar .acc {
  background: #3a4660;
  width: 100px;
  height: 90px;
  border-radius: 50%;
  position: absolute;
  left: 88em;
  top: 1em;
  cursor: pointer;
}
.acc p {
  font-family: Rufina;
  font-size: 55px;
  position: absolute;
  left: 0.5em;
  top: -0.8em;
  color: white;
}

h1 {
  font-family: cursive;
  font-style: normal;
  font-weight: 600;
  font-size: 60px;
  line-height: 113px;
  text-align: center;
  position: relative;
  top: 2.5em;
  color: #30384a;
}
h4 {
  font-family: cursive;
  font-style: normal;
  font-weight: 600;
  font-size: 40px;
  line-height: 113px;
  text-align: center;
  position: relative;
  top: 2.5em;
  color: #30384a;
}
.words button {
  background: #c9af98;
  border-radius: 40px;
  width: 190px;
  height: 70px;
  border: none;
  font-size: 25px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.52);
  color: white;
  font-weight: 800;
  position: relative;
  left: 27em;
  top: 4em;
}
.words a {
  text-decoration: none;
  color: white;
}
.words a:hover {
  color: #3a4660;
}

.login-word {
  font-family: Satisfy;
  color: #3a4660;
  position: absolute;
  font-size: 70px;
  left: 9.8em;
  top: 1em;
}
body {
  background: #c9af98;
}
.login .login-Link {
  display: none;
}
.login form input {
  background: #fffefe;
  border-radius: 10px;
  height: 35px;
  width: 466px;
  box-shadow: none;
  border-radius: 34px;
  border: none;
  font-size: 20px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.login form {
  position: absolute;
  left: 33em;
  top: 22em;
}
.login form input[type="button"] {
  font-family: Rockwell Extra Bold;
  font-weight: 800;
  background: #3a4660;
  border-radius: 40px;
  color: white;
  cursor: pointer;
}
.login .navbar .acc {
  display: none;
}

.signin form {
  position: absolute;
  left: 34em;
  top: 20em;
}
.signin input[type="text"] {
  width: 200px;

  height: 35px;
  border-radius: 34px;
  border: none;
  background: #fffefe;
}
.signin input {
  font-family: Rockwell Extra Bold;
  font-weight: 800;
  font-size: 18px;
  caret-color: transparent;
}
.signin input[type="email"],
input[type="password"] {
  background: #fffefe;
  height: 35px;
  width: 466px;
  box-shadow: none;
  border-radius: 34px;
  border: none;
  font-size: 16px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
.signin .login-Link {
  display: none;
}
.signin form input[type="button"] {
  font-family: Rockwell Extra Bold;
  font-weight: 800;
  background: #3a4660;
  border-radius: 40px;
  color: white;
  cursor: pointer;
  border: none;
  height: 35px;
  width: 466px;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
}

.codes {
  background-image: url(/sm/static/media/bg.17dd4346.jpg);
  background-repeat: no-repeat;
  position: absolute;
  background-size: cover;
  height: 100%;
  width: 100%;
}
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  overflow: scroll;
  overflow-x: hidden;
  background: linear-gradient(180deg, #845007 0%, rgba(196, 196, 196, 0) 100%);
}
.codes .login-Link {
  display: none;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.card {
