}

.codecontent {
  background-image: url(/sm/static/media/bg.17dd4346.jpg);
  background-repeat: no-repeat;
  position: absolute;
  background-size: cover;
  height: 100%;
  width: 100%;
}
.container {
  height: 100%;
  width: 100%;
