body,
html {
  height: 100%;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.52);
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #3a4660;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  width: 500px;
  right: 6.2em;
  height: 90px;
  border-radius: 46px;
}
.dropdown-content a {
  color: white;
  margin-left: 1.5em;
  margin-top: 0.8em;
  padding: 1em 2em;
  text-decoration: none;
  display: inline-block;
  border: 3px solid #845007;
  border-radius: 34px;
  font-size: 18px;
}
.acc:hover .dropdown-content {
  display: block;
}
.dropdown-content a:hover {
  background-color: #a08560;
}
