.codes {
  background-image: url(bg.jpg);
  background-repeat: no-repeat;
  position: absolute;
  background-size: cover;
  height: 100%;
  width: 100%;
}
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  overflow: scroll;
  overflow-x: hidden;
  background: linear-gradient(180deg, #845007 0%, rgba(196, 196, 196, 0) 100%);
}
.codes .login-Link {
  display: none;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
