.card {
  position: relative;
  width: 400px;
  border-radius: 7%;
  height: 312px;
  background: #c9af98;
  left: 5em;
  top: 13em;
  display: inline-block;
  margin-left: 3.5em;
  margin-top: 2em;
}
.card p {
  font-family: Rubik One;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 50px;
  display: flex;
  align-items: center;
  text-align: center;
  padding-top: 0.7em;
  color: #423333;
}
