.signin form {
  position: absolute;
  left: 34em;
  top: 20em;
}
.signin input[type="text"] {
  width: 200px;

  height: 35px;
  border-radius: 34px;
  border: none;
  background: #fffefe;
}
.signin input {
  font-family: Rockwell Extra Bold;
  font-weight: 800;
  font-size: 18px;
  caret-color: transparent;
}
.signin input[type="email"],
input[type="password"] {
  background: #fffefe;
  height: 35px;
  width: 466px;
  box-shadow: none;
  border-radius: 34px;
  border: none;
  font-size: 16px;
  appearance: none;
}
.signin .login-Link {
  display: none;
}
@import url(//db.onlinewebfonts.com/c/c373d9005cc5dfa6115be09d6d1a6dc9?family=Rockwell+Nova);
.signin form input[type="button"] {
  font-family: Rockwell Extra Bold;
  font-weight: 800;
  background: #3a4660;
  border-radius: 40px;
  color: white;
  cursor: pointer;
  border: none;
  height: 35px;
  width: 466px;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
}
